/* Base Styles */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1-6;
  color: #333;
  background-color: #f8f9fa;
}

.container {
  max-width: 1200px;
  margin: 0;
  padding: 0 20px;
}

/* Header */

.header {
  background: white;
  box-shadow: 0 2px 10px rgba(0, 0, 0, 1);
  position: sticky;
  top: 0;
  z-index: 100;
}

.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 20px;
}

.logo {
  font-size: 1.8rem;
  font-weight: bold;
  color: #2c3e50;
  text-decoration: none;
}

.nav {
  display: flex;
  gap: 2rem;
}

.nav a {
text-decoration: none;
color: #666;
font-weight: 500;
transition: color 0.3s ease;
justify-content: space-between;
}

.nav a:hover {
  color: #3498db;
}

/* Hero Section */
.hero {
  background: linear-gradient(135deg, #667eaa 0%, #764ba2 100%);
  color: white;
  padding: 4rem 0;
  text-align: center;
}

.hero-content {
  max-width: 800px;
  margin: 0 auto;
}

.hero-title {
  font-size: 1.3rem;
  margin-bottom: 1.5rem;
  opacity: 0.9;
}

.hero-description {
  font-size: 1.1rem;
  margin-bottom: 2rem;
  opacity: 0.8;
  line-height: 1.6;
}

.hero-buttons {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

/* Buttons */
.btn {
  padding: 12px 24px;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3 ease;
  display: inline-block;
}

.btn-primary {
  background: #3498db;
  color: white;
}

.btn-primary:hover {
  background: #2980b9;
  transform: translateY(-2px);
}

.btn-secondary {
  background: transparent;
  color: white;
  border: 2px solid white;
}

.btn-seconday:hover {
  background: white;
  color: #667eea;
}

/* Section Styles */

section {
  padding: 4rem 0;
}

.section-title {
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 1rem;
  color: #2c3e50;

}

.section-subtitle {
  text-align: center;
  font-size: 1.2rem;
  color: #666;
  margin-bottom: 3rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

/* About Section */

.about {
  background: white;
}

.about-content {
  max-width: 800px;
  margin: 0 auto 3rem auto;
  font-size: 1.1rem;
  text-align: center;
}

.about-content p {
  margin-bottom: 1.5rem;
}

.skills h3 {
  text-align: center;
  margin-bottom: 2rem;
  font-size: 1.8rem;
  color: #2c3e50;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  max-width: 1000px;
  margin: 0 auto;
}

.skill-category {
  background: #f8f9fa;
  padding: 1.5rem;
  border-radius: 8px;
  border-left: 4px solid #3498db;

}

.skill-category h4 {
  margin-bottom: 1rem;
  color: #2c3e50;
  font-size: 1.2rem;
}

.skill-category ul {
  list-style: none;
}

.skill-category li {
  padding: 0.3rem;
  color: #666;
}

.skill-category li::before {
  content: "->";
  color: #3498db;
  font-weight: bold;
}

/* Projects Section */

.projects {
  background: #f8f9fa;
}

.projectts-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.project-card {
  background: white;
  border-radius: 12px;
  padding: 2rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3 ease;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15);
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.prohect-header h3 {
  font-size: 1.3rem;
  color: #2c3e50;
}

.project-status {
  padding: 0.3rem 0.8rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 600;
  background: #e8f5e8;
  color: #27ae60;
}

.project-card .project-status:nth-child(2) {
  background: #fff3cd;
  color: #855404;
}

.project-description {
  color: #666;
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-tag {
  background: #e3f2fd;
  color: #1976d2;
  padding: 0.4rem 0.8rem;
  border-radius: 15px;
  font-size: 0.85rem;
  font-weight: 500;
}

/* Contact Section */

.contact {
  background: white;
}

.contact-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 3rem;
  max-width: 900px;
  margin: 0 auto;
}

.contact-info h3,
.availability h3 {
  margin-bottom: 1rem;
  color: #2c3e50;
  font-size: 1.5rem;
}

.contact-info p,
.availailty p {
  margin-bottom: 1.5rem;
  color: #666;
}

.contact-link {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  text-decoration: none;
  color: #3498db;
  font-weight: 500;
  padding: 0.8rem;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}

.contact-link::hover {
  background: #f8f9fa;
}

.contact-icon {
  font-size: 1.2rem;
}

.availability ul {
  list-style: none;
  margin-bottom: 1.5rem;
}

.availability li {
  padding: 0.3rem 0;
  color: #666;
}

.availability li::before {
  content: "âœ“ ";
  color: #27ae60;
  font-weight: bold;
}

.location {
  font-weight: 600;
  color: #3498db;
}

/* Footer */

.footer {
  background: #2c3e50;
  color: white;
  text-align: center;
  padding: 1.5rem 0;
}

.footer p {
  opacity: 0.8;
}

/* Responsive Desing */

@media (max-width: 768px) {
  .hero-title {
    font-size: 2.2rem;
  }

  .hero-subtitle {
    font-size: 1.1rem;
  }

  .hero-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .btn {
    width: 200px;
    text-align: center;
  }
  
  .nav {
    gap: 1rem;
  }
  
  .projects-grid {
    grid-template-columns: 1fr;
  }
  
  .skills-grid {
    grid-template-columns: 1fr;
  }
  
  .contact-content {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 0 15px;
  }
  
  .header .container {
    flex-direction: column;
    gap: 1rem;
  }
  
  .hero-title {
    font-size: 1.8rem;
  }
  
  section {
    padding: 2rem 0;
  }
  
  .section-title {
    font-size: 2rem;
  }
}